/**
 * Class description
 */
mh['GridTargeter'] = function (require, World, Tools, Base) {
    var World = mh['World'];
    var Tools = mh['Tools'];
    var GridTargeter = function (entity, settings) {
        mh['Base'].call(this);
        this.searchInterval = 1000;
        this.scanRadius = 400;
        this.collisionId = entity.collisionId;
        this.target = null;
        Tools.merge(this, settings);
        this.entity = entity;
        this._nearby = new LinkedList();
        this._grid = World.broadphase;
        this._timer = 0;
        this.position = entity.position;
    };
    GridTargeter.accessor = 'gridTargeter';
    GridTargeter.className = 'GRID_TARGETER';
    GridTargeter.priority = 10;
    GridTargeter.prototype = {
        constructor: GridTargeter,
        activate: function () {
            this.active = true;
            this.target = null;
            this.collisionId = this.entity.collisionId;
            this._findTarget();
        },
        disable: function () {
            this.active = false;
            this.target = null;
        },
        update: function () {
            if (performance.now() - this._timer >= this.searchInterval) {
                this._findTarget();
                this._timer = performance.now();
            }
        },
        dispose: function () {
            this.entity = null;
            this.position = null;
        },
        _findTarget: function () {
            var node, obj, m;
            this._nearby.clear();
            this._grid.getNearby(this.position, this.scanRadius, this._nearby);
            node = this._nearby.first;
            while (node) {
                obj = node.obj;
                if (obj && obj.entity.id !== this.entity.id) {
                    this.target = obj;
                    return;
                }
                node = node.next;
            }
            this.target = null;
        }
    };
    return GridTargeter;
}({}, mh['World'], mh['Tools'], mh['Base']);
