/**
 * @source https://github.com/martinwells/gamecore.js
 * Hoisted to the global namespace for convenience.
 */
mh['LinkedList'] = function () {
    var LinkedListNode = function () {
        this.obj = null;
        this.next = null;
        this.prev = null;
        this.free = true;
    };
    window.LinkedListNode = LinkedListNode;
    var LinkedList = function () {
        this.first = null;
        this.last = null;
        this.length = 0;
        this.objToNodeMap = {};
        this.uniqueId = Date.now() + '' + Math.floor(Math.random() * 1000);
        this.priority = 0;
        this.getNode = function (obj) {
            return this.objToNodeMap[obj.uniqueId];
        };
        this.addNode = function (obj) {
            var node = new LinkedListNode();
            node.obj = obj;
            node.prev = null;
            node.next = null;
            node.free = false;
            this.objToNodeMap[obj.uniqueId] = node;
            return node;
        };
        this.swapObjects = function (node, newObj) {
            this.objToNodeMap[node.obj.uniqueId] = null;
            this.objToNodeMap[newObj.uniqueId] = node;
            node.obj = newObj;
        };
        this.add = function (obj) {
            var node = this.objToNodeMap[obj.uniqueId];
            if (!node) {
                node = this.addNode(obj);
            } else {
                if (node.free === false)
                    return;
                node.obj = obj;
                node.free = false;
                node.next = null;
                node.prev = null;
            }
            if (!this.first) {
                this.first = node;
                this.last = node;
                node.next = null;
                node.prev = null;
            } else {
                if (this.last == null) {
                    throw new Error('Hmm, no last in the list -- that shouldn\'t happen here');
                }
                this.last.next = node;
                node.prev = this.last;
                this.last = node;
                node.next = null;
            }
            this.length++;
            if (this.showDebug)
                this.dump('after add');
        };
        this.has = function (obj) {
            return !!this.objToNodeMap[obj.uniqueId];
        };
        this.moveUp = function (obj) {
            this.dump('before move up');
            var c = this.getNode(obj);
            if (!c)
                throw 'Oops, trying to move an object that isn\'t in the list';
            if (c.prev == null)
                return;
            var b = c.prev;
            var a = b.prev;
            if (c == this.last)
                this.last = b;
            var oldCNext = c.next;
            if (a)
                a.next = c;
            c.next = b;
            c.prev = b.prev;
            b.next = oldCNext;
            b.prev = c;
            if (this.first == b)
                this.first = c;
        };
        this.moveDown = function (obj) {
            var b = this.getNode(obj);
            if (!b)
                throw 'Oops, trying to move an object that isn\'t in the list';
            if (b.next == null)
                return;
            var c = b.next;
            this.moveUp(c.obj);
            if (this.last == c)
                this.last = b;
        };
        this.sort = function (compare) {
            var sortArray = [];
            var i, l, node = this.first;
            while (node) {
                sortArray.push(node.object());
                node = node.next();
            }
            this.clear();
            sortArray.sort(compare);
            l = sortArray.length;
            for (i = 0; i < l; i++) {
                this.add(sortArray[i]);
            }
        };
        this.remove = function (obj) {
            var node = this.getNode(obj);
            if (node == null || node.free == true) {
                return false;
            }
            if (node.prev != null)
                node.prev.next = node.next;
            if (node.next != null)
                node.next.prev = node.prev;
            if (node.prev == null)
                this.first = node.next;
            if (node.next == null)
                this.last = node.prev;
            node.free = true;
            node.prev = null;
            node.next = null;
            this.length--;
            return true;
        };
        this.shift = function () {
            var node = this.first;
            if (this.length === 0)
                return null;
            if (node.prev) {
                node.prev.next = node.next;
            }
            if (node.next) {
                node.next.prev = node.prev;
            }
            this.first = node.next;
            if (!node.next)
                this.last = null;
            node.free = true;
            node.prev = null;
            node.next = null;
            this.length--;
            return node.obj;
        };
        this.pop = function () {
            var node = this.last;
            if (this.length === 0)
                return null;
            if (node.prev) {
                node.prev.next = node.next;
            }
            if (node.next) {
                node.next.prev = node.prev;
            }
            this.last = node.prev;
            if (!node.prev)
                this.first = null;
            node.free = true;
            node.prev = null;
            node.next = null;
            this.length--;
            return node.obj;
        };
        this.concat = function (list) {
            var node = list.first;
            while (node) {
                this.add(node.obj);
                node = node.next;
            }
        };
        this.clear = function () {
            var next = this.first;
            while (next) {
                next.free = true;
                next = next.next;
            }
            this.first = null;
            this.length = 0;
        };
        this.dispose = function () {
            var next = this.first;
            while (next) {
                next.obj = null;
                next = next.next;
            }
            this.first = null;
            this.objToNodeMap = null;
        };
        this.dump = function (msg) {
            console.log('====================' + msg + '=====================');
            var a = this.first;
            while (a != null) {
                console.log('{' + a.obj.toString() + '} previous=' + (a.prev ? a.prev.obj : 'NULL'));
                a = a.next();
            }
            console.log('===================================');
            console.log('Last: {' + (this.last ? this.last.obj : 'NULL') + '} ' + 'First: {' + (this.first ? this.first.obj : 'NULL') + '}');
        };
    };
    window.LinkedList = LinkedList;
    return LinkedList;
}();
