mh['Health'] = function (require, Tools, Base) {
    var Tools = mh['Tools'];
    var Health = function (entity, settings) {
        mh['Base'].call(this);
        this.max = 100;
        this.overage = 0;
        this.alive = false;
        this.active = false;
        Tools.merge(this, settings);
        this.onDeath = new Signal();
        this.onDamage = new Signal();
        this.onHeal = new Signal();
        this.onFull = new Signal();
        this.onActivate = new Signal();
        this.entity = entity;
        this._meter = 0;
    };
    Health.accessor = 'health';
    Health.className = 'HEALTH';
    Health.priority = 1;
    Health.prototype = {
        constructor: Health,
        activate: function () {
            this._meter = this.max;
            this.alive = true;
            this.onActivate.dispatch(this._meter, this.entity);
        },
        change: function (amount) {
            if (!this.alive)
                return 0;
            this._meter += amount;
            if (this._meter <= 0) {
                this._meter = 0;
                this.alive = false;
                this.onDeath.dispatch(amount, this.entity);
            } else {
                if (this._meter > this.max + this.overage) {
                    this._meter = this.max;
                    this.onFull.dispatch(amount);
                }
                if (amount < 0)
                    this.onDamage.dispatch(amount);
                else
                    this.onHeal.dispatch(amount);
            }
            return this._meter;
        },
        drain: function (amount) {
            if (!this.alive)
                return 0;
            if (this._meter > this.max) {
                this._meter -= amount;
                if (this._meter <= this.max) {
                    this._meter = this.max;
                    this.onFull.dispatch(amount);
                }
            }
            return this._meter;
        },
        dispose: function () {
            this.onDeath.dispose();
            this.onDamage.dispose();
            this.onHeal.dispose();
            this.onFull.dispose();
            this.entity = null;
            this.deathSignal = null;
            this.onDamage = null;
            this.onHeal = null;
            this.onFull = null;
        }
    };
    return Health;
}({}, mh['Tools'], mh['Base']);
