/*
	The Engine holds the state manager, fires it off when the document is ready, and runs the one and only update loop. It ties all the other core modules together, setting everything up.
	
	@author Corey Birnbaum http://coldconstructs.com/ @vonWolfehaus
*/
mh['Engine'] = function (require, Kai, CommTower, StateManager, RequestAnimationFrame, Loader, Cache, MouseController, KeyboardController) {
    var Kai = mh['Kai'];
    var Tower = mh['CommTower'];
    var StateManager = mh['StateManager'];
    var RAF = mh['RequestAnimationFrame'];
    var Loader = mh['Loader'];
    var Cache = mh['Cache'];
    var MouseController = mh['MouseController'];
    var KeyboardController = mh['KeyboardController'];
    var Engine = function () {
        if (Kai.debugMessages) {
            console.log('[Engine] Initializing');
        }
        this.state = new StateManager();
        this.raf = new RAF(this);
        this._paused = false;
        var self = this;
        this._onInit = function () {
            return self.init();
        };
        if (document.readyState === 'complete' || document.readyState === 'interactive') {
            window.setTimeout(this._onInit, 0);
        } else {
            document.addEventListener('DOMContentLoaded', this._onInit, false);
            window.addEventListener('load', this._onInit, false);
        }
    };
    Engine.prototype = {
        constructor: Engine,
        init: function () {
            if (Kai.ready) {
                return;
            }
            var self = this;
            if (!document.body) {
                window.setTimeout(this._onInit, 20);
                return;
            }
            document.removeEventListener('DOMContentLoaded', this._onInit);
            window.removeEventListener('load', this._onInit);
            window.addEventListener('focus', function (evt) {
                self._paused = false;
                Tower.resume.dispatch();
            }, false);
            window.addEventListener('blur', function (evt) {
                self._paused = true;
                Tower.pause.dispatch();
            }, false);
            Kai.engine = this;
            Kai.mouse = new MouseController();
            Kai.keys = new KeyboardController();
            Kai.cache = new Cache();
            Kai.load = new Loader();
            Kai.ready = true;
            this.state.init();
            Kai.inputBlocked = false;
            if (Kai.debugMessages) {
                console.log('[Engine] Ready');
            }
            this.raf.start();
        },
        start: function (state) {
            if (Kai.ready) {
                return;
            }
            this.state.switchState(state);
        },
        update: function () {
            var i, node, obj, list = Kai.componentsSorted, len = list.length;
            if (this._paused) {
                return;
            }
            if (this.state.ready) {
                if (Kai.debugCtx) {
                    Kai.debugCtx.clearRect(0, 0, Kai.width, Kai.height);
                }
                for (i = 0; i < len; i++) {
                    if (!list[i])
                        continue;
                    node = list[i].first;
                    if (node && !node.obj.update) {
                        continue;
                    }
                    while (node) {
                        obj = node.obj;
                        if (obj.active) {
                            obj.update();
                        }
                        node = node.next;
                    }
                }
                this.state.currentState.update();
            } else {
            }
            if (Kai.renderHook) {
                Kai.renderHook();
            }
        },
        dispose: function () {
        }
    };
    return Engine;
}({}, mh['Kai'], mh['CommTower'], mh['StateManager'], mh['RequestAnimationFrame'], mh['Loader'], mh['Cache'], mh['MouseController'], mh['KeyboardController']);
