mh['Timer'] = function (require, Tools, Base) {
    var Tools = mh['Tools'];
    var Timer = function (entity, settings) {
        mh['Base'].call(this);
        this.repeat = -1;
        this.immediateDispatch = false;
        this.interval = 1000;
        Tools.merge(this, settings);
        this.onInterval = new Signal();
        this.entity = entity;
        this._timer = 0;
        this._numTicks = 0;
        this._currentRepeat = this.repeat;
        this.disable();
    };
    Timer.accessor = 'timer';
    Timer.className = 'TIMER';
    Timer.priority = 13;
    Timer.prototype = {
        constructor: Timer,
        activate: function () {
            this._timer = performance.now();
            this.active = true;
            this._currentRepeat = this.repeat;
            this._numTicks = 0;
            if (this.immediateDispatch) {
                this._numTicks++;
                this.onInterval.dispatch(this._numTicks);
            }
        },
        disable: function () {
            this.active = false;
        },
        reset: function () {
            this._timer = performance.now();
            this._numTicks = 0;
            if (this.active && this.immediateDispatch) {
                this._numTicks++;
                this.onInterval.dispatch(this._numTicks);
            }
        },
        update: function () {
            if (performance.now() - this._timer >= this.interval) {
                this._numTicks++;
                this.onInterval.dispatch(this._numTicks);
                if (this.repeat !== -1 && this._currentRepeat-- === 0) {
                    this.disable();
                    return;
                }
                this._timer = performance.now();
            }
        },
        dispose: function () {
            this.onInterval.dispose();
            this.onInterval = null;
            this.entity = null;
        }
    };
    return Timer;
}({}, mh['Tools'], mh['Base']);
