// JavaScript utility functions
mh['Tools'] = {
    PI: Math.PI,
    TAU: Math.PI * 2,
    DEG_TO_RAD: 0.0174532925,
    RAD_TO_DEG: 57.2957795,
    clamp: function (val, min, max) {
        return Math.max(min, Math.min(max, val));
    },
    sign: function (val) {
        return val && val / Math.abs(val);
    },
    random: function (min, max) {
        if (arguments.length === 1) {
            return Math.random() * min - min * 0.5;
        }
        return Math.random() * (max - min) + min;
    },
    randomInt: function (min, max) {
        if (arguments.length === 1) {
            return Math.floor(Math.random() * min - min * 0.5);
        }
        return Math.floor(Math.random() * (max - min + 1) + min);
    },
    normalize: function (v, min, max) {
        return (v - min) / (max - min);
    },
    getShortRotation: function (angle) {
        angle %= this.TAU;
        if (angle > this.PI) {
            angle -= this.TAU;
        } else if (angle < -this.PI) {
            angle += this.TAU;
        }
        return angle;
    },
    generateID: function () {
        return Math.random().toString(36).slice(2) + Date.now();
    },
    isPlainObject: function (obj) {
        if (typeof obj !== 'object' || obj.nodeType || obj === obj.window) {
            return false;
        }
        try {
            if (obj.constructor && !Object.prototype.hasOwnProperty.call(obj.constructor.prototype, 'isPrototypeOf')) {
                return false;
            }
        } catch (e) {
            return false;
        }
        return true;
    },
    merge: function () {
        var options, name, src, copy, copyIsArray, clone, target = arguments[0] || {}, i = 1, length = arguments.length, deep = false;
        if (typeof target === 'boolean') {
            deep = target;
            target = arguments[1] || {};
            i = 2;
        }
        for (; i < length; i++) {
            if ((options = arguments[i]) !== null) {
                for (name in options) {
                    src = target[name];
                    copy = options[name];
                    if (target === copy) {
                        continue;
                    }
                    if (deep && copy && (this.isPlainObject(copy) || (copyIsArray = Array.isArray(copy)))) {
                        if (copyIsArray) {
                            copyIsArray = false;
                            clone = src && Array.isArray(src) ? src : [];
                        } else {
                            clone = src && this.isPlainObject(src) ? src : {};
                        }
                        target[name] = this.extend(deep, clone, copy);
                    } else if (copy !== undefined) {
                        target[name] = copy;
                    }
                }
            }
        }
        return target;
    },
    now: function () {
        return window.nwf ? window.nwf.system.Performance.elapsedTime : window.performance.now();
    },
    empty: function (node) {
        while (node.lastChild) {
            node.removeChild(node.lastChild);
        }
    },
    radixSort: function (arr, idx_begin, idx_end, bit) {
        idx_begin = idx_begin || 0;
        idx_end = idx_end || arr.length;
        bit = bit || 31;
        if (idx_begin >= idx_end - 1 || bit < 0) {
            return;
        }
        var idx = idx_begin;
        var idx_ones = idx_end;
        var mask = 1 << bit;
        while (idx < idx_ones) {
            if (arr[idx] & mask) {
                --idx_ones;
                var tmp = arr[idx];
                arr[idx] = arr[idx_ones];
                arr[idx_ones] = tmp;
            } else {
                ++idx;
            }
        }
        this.radixSort(arr, idx_begin, idx_ones, bit - 1);
        this.radixSort(arr, idx_ones, idx_end, bit - 1);
    }
};
